<template>
  <div id="app">
    <h1>To Do List</h1>

    <!-- <label v-for="label in options" >
      <input type="radio"
        v-model="current"
        v-bind:value="label.value">{{ label.label }}
    </label> -->

    （{{ computedTodos.length }}/Page）
    
        <h2>Add ToDo List</h2>
    <form class="add-form" v-on:submit.prevent="doAdd">
      Comment<input type="text" ref="comment">
      <button type="submit">ADD</button>
    </form>

    <table>
      <thead v-pre>
        <tr>
          <th class="id">ID</th>
          <th class="comment">Comment</th>
          <th class="state">Status</th>
          <th class="button">-</th>
        </tr>
      </thead>

      <tbody>
        <tr
          v-for="item in computedTodos"
          v-bind:key="item.id"
          v-bind:class="{done:item.state}">
          <th>{{ item.id }}</th>
          <td>{{ item.comment }}</td>
          <td class="state">
            <button v-on:click="doChangeState(item)">
              {{ labels[item.state] }}
            </button>
          </td>
          <td class="button">
            <button v-on:click.ctrl="doRemove(item)">
              REMOVE
            </button>
          </td>
        </tr>
      </tbody>

    </table>

  </div>
</template>

<script>
import todoStorage from "./main";

export default {
  name: "App",
  data:()=>{ return {
    todos: [],
    current: -1,
    options: [
      { value: -1, label: 'ALL' },
      { value: 0, label: 'Working' },
      { value: 1, label: 'Accomplished' }
    ]
  }
  },

  computed: {
    computedTodos: function () {
      return this.todos.filter(function (el) {
        return this.current < 0 ? true : this.current === el.state
      }, this)
    },

    labels() {
      return this.options.reduce(function (a, b) {
        return Object.assign(a, { [b.value]: b.label })
      }, {})
    }
  },

  watch: {
    todos: {
      handler: function (todos) {
        todoStorage.save(todos)
      },
      deep: true
    }
  },
  created() {
    this.todos = todoStorage.fetch()
  },

  methods: {
    // doAdd: function(event, value) {
    doAdd: () => {

      var comment = this.$refs.comment
      if (!comment.value.length) {
        return
      }
      this.todos.push({
        id: todoStorage.uid++,
        comment: comment.value,
        state: 0
      })
      comment.value = ''
    },
    doChangeState: function (item) {
      item.state = !item.state ? 1 : 0
    },
    doRemove: function (item) {
      var index = this.todos.indexOf(item)
      this.todos.splice(index, 1)
    }
  }

};
</script>

